.banner {
  width: 100%;
  height: 100%;
  background: url(https://images.unsplash.com/photo-1506846540181-347008948fff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=593&q=80) no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 167px 772px 167px 180px;
}

.banner button {
  width: 160px;
  height: 45px;
}

.menu ul {
  max-width: 730px;
}

.hot-exhibition {
  max-width: 1110px;
}

.hot-exhibition .container .exhibition-img img {
  width: 350px;
  height: 350px;
}

.hot-exhibition .container .exhibition-content .content-main {
  width: 90%;
  height: 34%;
}

.hot-exhibition .container .exhibition-content .content-btn {
  margin-top: 34px;
}

.hot-exhibition .container .exhibition-content .content-btn button {
  line-height: 1.5;
  padding: 10px 16px;
}

.hot-exhibition .container .exhibtion-date p {
  width: 39px;
  height: 146px;
}

.hot-exhibition .hot-exhibition-center {
  margin: 20px 0;
}

.live-replay {
  max-width: 1110px;
}

.like-guess {
  max-width: 1110px;
}

.like-guess .like-guess-exhibition ul li img {
  height: 320px;
  width: 320px;
}

.sencond-banner {
  width: 100%;
  height: 100%;
  background: url(https://images.unsplash.com/photo-1536768767433-cc552f38def9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=750&q=80);
  background-position: center center;
  background-size: cover;
  padding: 208px 750px 36px 85px;
}

.sencond-banner .banner-content {
  width: 445px;
  height: 96px;
  line-height: 1.5;
}

.sencond-banner .subscribe input {
  height: 48px;
}

.sencond-banner .subscribe input[type="text"] {
  padding: 12px 64px 12px 12px;
}

.sencond-banner .subscribe input[type="button"] {
  padding: 12px 52px;
}
